<template>
  <div class="data-center">
    <div class="content">
      <a-button type="primary" class="create" @click="create">创建</a-button>
      <a-table
      :columns="columns"
      :data-source="data"
      :pagination="pagination"
      @change="handleTableChange">
        <!-- eslint-disable -->
        <template slot="operation" slot-scope="text, record">
          <span class="operation-item edit-btn" @click="handleEdit(record)">编辑</span>
          <span class="operation-item" @click="deleteUser(record)">删除</span>
        </template>
        <!-- eslint-disable -->
        <template slot="userType" slot-scope="text, record">
          {{text === 0 ? '普通用户' : '管理员'}}
        </template>
      </a-table>
    </div>
    <UserEditOrAdd
    ref="UserEditOrAdd"
    @comfirmUser="comfirmUser"
    :title="title"></UserEditOrAdd>
  </div>
</template>
<script>
import UserEditOrAdd from '@/components/UserEditOrAdd'
import { mapGetters } from 'vuex'
import { modifyUser, getAllUserInfo, deleteUser, addUser } from '@/apis/user-management'
const columns = [
  // {
  //   title: '序号',
  //   dataIndex: 'num',
  //   key: 'num'
  // },
  {
    title: '姓名',
    dataIndex: 'name',
    key: 'name'
  },
  {
    title: '用户类型',
    dataIndex: 'userType',
    key: 'userType',
    ellipsis: true,
    scopedSlots: { customRender: 'userType' }
  },
  {
    title: '操作',
    dataIndex: 'operation',
    key: 'operation',
    scopedSlots: { customRender: 'operation' }
  }
]

const defaultUser = {
  name: '',
  password: '',
  userType: undefined
}
export default {
  name: 'user-management',
  components: { UserEditOrAdd },
  data () {
    return {
      columns: columns,
      data: [],
      title: '创建用户',
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0,
        showQuickJumper: true
      },
      searchInfo: {
        pageSize: 10,
        pageNum: 1
      },
      curEditUser: { }
    }
  },
  computed: {
    ...mapGetters(['userInfo'])
  },
  mounted () {
    this.getAllUserInfo(this.searchInfo)
  },
  methods: {
    handleEdit (record) {
      this.curEditUser = { ...record }
      this.title = '编辑用户'
      this.$refs.UserEditOrAdd.visible = true
      const param = {
        name: record.name,
        password: record.password,
        checkPass: record.password,
        userType: record.userType
      }
      this.$refs.UserEditOrAdd.ruleForm = param
    },
    deleteUser (data) {
      const $this = this
      this.$confirm({
        content: '确定删除吗？',
        onOk () {
          deleteUser({
            id: data.id
          }).then(res => {
            if (res.success) {
              $this.$message.success('删除成功')
              $this.getAllUserInfo($this.searchInfo)
            } else {
              $this.$message.error('删除失败')
            }
          })
        }
      })
    },
    create () {
      this.title = '创建用户'
      this.$refs.UserEditOrAdd.visible = true
      this.$refs.UserEditOrAdd.ruleForm = { ...defaultUser, ...{ checkPass: '' } }
    },
    comfirmUser (data) {
      const params = {
        name: data.name,
        password: data.password,
        userType: data.userType
      }
      if (this.title === '编辑用户') {
        this.modifyUser(params)
      } else {
        this.addUser(params)
      }
    },
    addUser (param) {
      return addUser({
        info: JSON.stringify(param)
      }).then(res => {
        if (res.success) {
          this.$message.success('新增成功')
          this.getAllUserInfo(this.searchInfo)
        } else {
          this.$message.error('新增失败')
        }
      })
    },
    modifyUser (param) {
      param.id = this.curEditUser.id
      return modifyUser({
        info: JSON.stringify(param)
      }).then(res => {
        if (res.success) {
          this.$message.success('编辑成功')
          this.getAllUserInfo(this.searchInfo)
        } else {
          this.$message.error('编辑失败')
        }
      })
    },
    getAllUserInfo (param) {
      return getAllUserInfo(param).then(res => {
        this.data = res.value.pageData ? res.value.pageData : []
        this.pagination.total = res.value.totalSize
        this.pagination.current = res.value.curPage
      })
    },
    handleTableChange (pagination, filters, sorter) {
      this.searchInfo.pageNum = pagination.current
      this.pagination.current = pagination.current
      this.getAllUserInfo(this.searchInfo)
    }
  }
}
</script>
<style lang="less" scoped>
.operation-item {
  color: #008dff;
  cursor: pointer;
}
.edit-btn {
  margin-right: 10px;
}
.create {
  margin-bottom: 10px;
}
</style>
