<template>
  <div class="v-header">
    <span>{{userInfo.name}}</span>
    <a-icon type="logout" @click="logout" class="logout-icon"/>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import { logout } from '@/apis/user-management'
export default {
  name: 'v-header',
  data () {
    return {

    }
  },
  computed: {
    ...mapGetters(['userInfo'])
  },
  methods: {
    logout () {
      this.$confirm({
        title: '退出提示',
        content: '确认退出吗？',
        okText: '确认',
        cancelText: '取消',
        onOk () {
          logout().then(res => {
            if (res.success) {
              window.location.href = './login.html'
            }
          })
        }
      })
    }
  }
}
</script>
<style>
.v-header {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 16px;
  padding: 0 20px;
}
.logout-icon {
  margin-left: 10px;
}
</style>
