/*!
 * 请求相关处理，基于 [axios](https://github.com/axios/axios)
 */

/* eslint prefer-promise-reject-errors: "off" */

import axios from 'axios'
import qs from 'qs'

// 根据实际情况自定义
const CONTEXT_PATH = '/CARTEST'

// 独立的 zone，不污染
// 默认配置，根据情况自定义
export const axiosInstance = axios.create({
  baseURL: process.env.NODE_ENV === 'development' ? CONTEXT_PATH : CONTEXT_PATH,
  withCredentials: true,
  // headers: { 'X-Requested-With': 'XMLHttpRequest', 'Content-Type': 'application/json' }
  headers: { 'X-Requested-With': 'XMLHttpRequest', 'Content-Type': 'application/x-www-form-urlencoded' }
  // timeout: 10 * 1000 // 5s
})

axiosInstance.interceptors.request.use(function (config) {
  const isFormData = config.data instanceof FormData
  if (!isFormData) {
    // 转为 Form 格式
    config.data = qs.stringify(config.data, { arrayFormat: 'brackets' })
  }
    return config
  },
  function(error) {
    Promise.error(error)
  }
)

// 响应处理
axiosInstance.interceptors.response.use(function (response) {
  if (!response.data) {
    return Promise.reject(new Error('服务异常'))
  }
  if (response.data) {
    // normalize
    response.data.data = response.data.value
  }
  return response.data
}, function (error) {
  switch (error.request.status) {
    case 0:
      return Promise.reject({ message: '服务异常' })
    case 404:
      return Promise.reject({ message: '接口服务不存在' })
  }
  switch (error.response.status) {
    case 403:
      return Promise.reject({ type: 'forbidden', message: '您无权限访问该资源' })
    case 500:
      return Promise.reject({ message: '服务异常' })
    case 504:
      return Promise.reject({ message: '网络超时' })
    default:
      return Promise.reject({ message: '未知 Status Code' })
  }
})
