import { axiosInstance } from '.'
import qs from 'qs'

// 车辆信息搜索
export const searchInfo = (param) => axiosInstance.post('/car/searchInfo?' + qs.stringify(param))

// 车辆信息单条编辑
export const modifyInfo = (param) => axiosInstance.post('/car/modifyInfo', param)

export const addCarInfo = (param) => axiosInstance.post('/car/addCarInfo', param)

// 上传excel
export const batchImportCarInfo = (param) => axiosInstance.post('/car/batchImportCarInfo', param)

// 登录
export const login = (param) => axiosInstance.post('/user/login', param)

// 获取用户信息
export const checkType = () => axiosInstance.post('/user/checkType')

// 编辑用户
export const modifyUser = (param) => axiosInstance.post('/user/modifyUser', param)

export const addUser = (param) => axiosInstance.post('/user/addUser', param)

// 删除用户
export const deleteUser = (param) => axiosInstance.post('/user/deleteUser', param)

// 获取用户列表
export const getAllUserInfo = (param) => axiosInstance.post('/user/getAllUserInfo', param)

export const logout = () => axiosInstance.post('/user/logout')

export const deleteCarInfo = (param) => axiosInstance.post('/car/deleteCarInfo', param)

// 导出
export const exportFile = (param) => {
  const str = qs.stringify(param)
  window.open('/CARTEST/car/downloadAllCar?' + str)
}
