<template>
  <div class="data-center">
    <DataCenterQuery @search="search"></DataCenterQuery>
    <div class="operation-wrap">
      <a-button type="primary" class="create" @click="create">创建</a-button>
      <a-button type="primary" @click="importNew" v-show="showFile">上传excel</a-button>
      <input type="file" :accept="acceptType" @change="uploadFile" ref="file" v-show="false">
      <a-button type="primary" class="down-file" @click="downFile" v-show="showFile">下载模板</a-button>
      <a-button type="primary" class="down-file" @click="exportFile" v-show="showFile">批量导出</a-button>
    </div>
    <div class="content">
      <a-table
      :pagination="pagination"
      :columns="columns"
      :data-source="data"
      @change="handleTableChange">
        <!-- eslint-disable -->
        <template slot="operation" slot-scope="text, record">
          <span class="operation-item edit-btn" @click="handleEdit(record)">编辑</span>
          <span class="operation-item" @click="deleteCarInfo(record)">删除</span>
        </template>
        <template slot="state" slot-scope="text, record">
          {{Number(text) === 1 ? '今年已来' : '没有来'}}
        </template>
      </a-table>
    </div>
    <DataAddOrEdit
    :title="title"
    @comfirm="comfirm"
    ref="DataAddOrEdit">
    </DataAddOrEdit>
  </div>
</template>
<script>
import DataCenterQuery from '@/components/DataCenterQuery'
import DataAddOrEdit from '@/components/DataAddOrEdit'
import { searchInfo, modifyInfo, batchImportCarInfo, addCarInfo, deleteCarInfo, exportFile } from '@/apis/user-management'
import moment from 'moment'
import { mapGetters } from 'vuex'
import pick from 'lodash/pick'
const columns = [
  // {
  //   title: '序号',
  //   dataIndex: 'id',
  //   key: 'id'
  // },
  {
    title: '车辆号码',
    dataIndex: 'carNumber',
    key: 'carNumber'
  },
  {
    title: '联系电话',
    dataIndex: 'phone',
    key: 'phone',
    ellipsis: true
  },
  {
    title: '检验日期',
    dataIndex: 'testDate',
    key: 'testDate',
    ellipsis: true
  },
  {
    title: '下次年审',
    dataIndex: 'nextDate',
    key: 'nextDate',
    ellipsis: true
  },
  {
    title: '状态',
    dataIndex: 'state',
    key: 'state',
    ellipsis: true,
    scopedSlots: { customRender: 'state' }
  },
  {
    title: '备注',
    dataIndex: 'commonLog',
    key: 'commonLog',
    ellipsis: true
  },
  {
    title: '操作',
    dataIndex: 'operation',
    key: 'operation',
    scopedSlots: { customRender: 'operation' }
  }
]

const defaultSearchInfo = {
  carNumber: '',
  phone: '',
  testStartDate: '',
  testEndDate: '',
  commonLog: '',
  nextStartDate: '',
  nextEndDate: '',
  state: '',
  pageNum: 1,
  pageSize: 10
}

export default {
  name: 'data-center',
  components: { DataCenterQuery, DataAddOrEdit },
  data () {
    return {
      columns: columns,
      data: [],
      acceptType: 'application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
      pagination: {
        current: 1,
        pageSize: 10,
        total: 0,
        showQuickJumper: true
      },
      title: '创建',
      curSearchInfo: { },
      curRecord: null
    }
  },
  computed: {
    ...mapGetters(['userInfo']),
    showFile () {
      // 1是管理员
      return this.userInfo.type === 1
    }
  },
  mounted () {
    this.curSearchInfo = { ...defaultSearchInfo }
    this.searchInfo(defaultSearchInfo)
  },
  methods: {
    search (info) {
      const params = this.transSearchInfo(info)
      this.curSearchInfo = params
      this.searchInfo(params)
    },
    exportFile () {
      const param = { ...this.curSearchInfo }
      delete param.pageNum
      delete param.pageSize
      exportFile(param)
    },
    searchInfo (params) {
      const formData = new FormData()
      Object.entries(params).forEach(([key, value]) => {
        formData.append(key, value)
      })
      return searchInfo(params).then(res => {
        if (res.success) {
          this.data = res.value.pageData ? res.value.pageData : []
          this.pagination.total = res.value.totalSize
          this.pagination.current = res.value.curPage
        }
      })
        .catch(() => {
          this.data = []
          this.pagination.total = 0
          this.pagination.current = 1
        })
    },
    handleTableChange (pagination, filters, sorter) {
      this.curSearchInfo.pageNum = pagination.current
      this.pagination.current = pagination.current
      this.searchInfo(this.curSearchInfo)
    },
    importNew () {
      this.$refs.file.click()
    },
    uploadFile (e) {
      console.log(e.target.files)
      const file = e.target.files[0]
      const formData = new FormData()
      formData.append('excelFile', file)
      return batchImportCarInfo(formData).then(res => {
        if (res.success) {
          console.log(this.curSearchInfo, 99)
          this.searchInfo(this.curSearchInfo)
          this.$message.success('上传成功')
        } else {
          this.$message.error('上传失败')
        }
      })
    },
    downFile () {
      window.open('/CARTEST/car/downloadDemo')
    },
    create () {
      this.title = '创建'
      this.$refs.DataAddOrEdit.visible = true
      const params = {
        carNumber: '',
        phone: '',
        commonLog: '',
        state: ''
      }
      delete params.pageNum
      delete params.pageSize
      this.$refs.DataAddOrEdit.ruleForm = params
    },
    comfirm (data) {
      this.modifyInfo(data)
    },
    deleteCarInfo (record) {
      const $this = this
      this.$confirm({
        content: '确定删除吗？',
        onOk () {
          deleteCarInfo({
            id: record.id
          }).then(res => {
            if (res.success) {
              $this.$message.success('删除成功')
              $this.searchInfo($this.curSearchInfo)
            } else {
              $this.$message.success('删除失败')
            }
          })
        }
      })
    },
    transSearchInfo (info) {
      const params = pick(info, ['carNumber', 'phone', 'commonLog', 'state'])
      if (info.testDate.length) {
        params.testStartDate = info.testDate[0]
        params.testEndDate = info.testDate[1]
      } else {
        params.testStartDate = ''
        params.testEndDate = ''
      }
      if (info.nextDate.length) {
        params.nextStartDate = info.nextDate[0]
        params.nextEndDate = info.nextDate[1]
      } else {
        params.nextStartDate = ''
        params.nextEndDate = ''
      }
      params.pageNum = 1
      params.pageSize = this.pagination.pageSize
      return params
    },
    handleEdit (record) {
      this.title = '编辑'
      this.curRecord = { ...record }
      this.$refs.DataAddOrEdit.visible = true
      const params = { ...record }
      params.testDate = moment(params.testDate)
      params.nextDate = moment(params.nextDate)
      this.$refs.DataAddOrEdit.ruleForm = params
    },
    modifyInfo (params) {
      params.nextDate = moment(params.nextDate).format('YYYY-MM-DD')
      params.testDate = moment(params.testDate).format('YYYY-MM-DD')
      let handleMethod = addCarInfo
      if (this.title === '编辑') {
        params.id = this.curRecord.id
        handleMethod = modifyInfo
      }
      const param = {
        param: JSON.stringify(params)
      }
      return handleMethod(param).then(res => {
        if (res.success) {
          this.$message.success(this.title + '成功')
          this.searchInfo(this.curSearchInfo)
        } else {
          this.$message.error(res.message)
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.content {
  margin-top: 20px;
}
.operation-wrap {
  margin-top: 10px;
}
.create {
  margin-right: 10px;
}
.down-file {
  margin-left: 10px;
}
.operation-item {
  color: #008dff;
  cursor: pointer;
}
.edit-btn {
  margin-right: 10px;
}
</style>
