<template>
  <div class="data-center-query">
    <div class="query-row">
      <div class="query-item">
        <span class="item-label">车辆号码：</span>
        <a-input placeholder="请输入" class="search-item" v-model="searchInfo.carNumber" @pressEnter="search"/>
      </div>
      <div class="query-item">
        <span class="item-label">联系电话：</span>
        <a-input placeholder="请输入" class="search-item" v-model="searchInfo.phone" @pressEnter="search"/>
      </div>
      <div class="query-item">
        <span class="item-label">检验日期：</span>
        <a-range-picker @change="onChangeTestDate" class="search-item" v-model="testDate"/>
      </div>
      <div class="query-item">
        <span class="item-label">备注：</span>
        <a-input placeholder="请输入" class="search-item" v-model="searchInfo.commonLog" @pressEnter="search"/>
      </div>
      <div class="query-item">
        <span class="item-label">下次年审：</span>
        <a-range-picker @change="onChangeNextDate" class="search-item" v-model="nextDate" />
      </div>
      <div class="query-item">
        <span class="item-label">状态：</span>
        <a-select style="width: 120px" v-model="searchInfo.state" placeholder="请选择" @change="search">
          <a-select-option :value="item.id" v-for="item in stateList" :key="item.id">
            {{item.name}}
          </a-select-option>
        </a-select>
        <!-- <a-input placeholder="请输入" class="search-item" v-model="searchInfo.status"/> -->
      </div>
    </div>
    <div class="btn-wrap">
      <a-button type="primary" class="search" @click="search">搜索</a-button>
      <a-button class="search" @click="reset">重置</a-button>
    </div>
  </div>
</template>
<script>

const defaultSearchInfo = {
  carNumber: '',
  phone: '',
  testDate: [],
  commonLog: '',
  nextDate: [],
  state: undefined
}
export default {
  name: 'data-center-query',
  data () {
    return {
      searchInfo: Object.assign({ }, defaultSearchInfo),
      testDate: [],
      nextDate: [],
      stateList: [
        {
          id: 1,
          name: '今年已来'
        },
        {
          id: 2,
          name: '没有来'
        }
      ]
    }
  },
  methods: {
    onChangeTestDate (date, dateString) {
      console.log(date, dateString)
      this.testDate = date
      this.searchInfo.testDate = dateString
      this.search()
    },
    onChangeNextDate (date, dateString) {
      console.log(date, dateString)
      this.nextDate = date
      this.searchInfo.nextDate = dateString
      this.search()
    },
    search () {
      console.log(this.searchInfo, 333)
      this.$emit('search', this.searchInfo)
    },
    reset () {
      this.searchInfo = { ...defaultSearchInfo }
      this.testDate = null
      this.nextDate = null
      this.search()
    }
  }
}
</script>
<style lang="less" scoped>
.data-center-query {
  display: flex;
  // flex-direction: column;
}
.query-row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.query-item {
  display: flex;
  align-items: center;
  margin-right: 20px;
  margin-top: 10px;
}
.item-label {
  flex: 1 0 auto;
}
.btn-wrap {
  display: flex;
  justify-content: flex-end;
  padding-top: 10px;
}
.search {
  margin-right: 10px;
}
.search-item {
  min-width: 150px;
}
</style>
