<template>
  <a-modal v-model="visible" :title="title" @ok="submitForm('ruleForm')" @cancel="resetForm('ruleForm')">
    <a-form-model ref="ruleForm" :model="ruleForm" :rules="rules" v-bind="layout">
      <a-form-model-item label="用户名" prop="name">
        <a-input v-model.number="ruleForm.name" autocomplete="new-password"/>
      </a-form-model-item>
      <a-form-model-item label="用户类型" prop="userType">
        <a-select v-model="ruleForm.userType" style="width: 120px">
          <a-select-option :value="0">
            普通用户
          </a-select-option>
          <a-select-option :value="1">
            管理员
          </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item label="密码" prop="password">
        <a-input v-model="ruleForm.password" type="password" autocomplete="new-password" />
      </a-form-model-item>
      <a-form-model-item label="确认密码" prop="checkPass">
        <a-input v-model="ruleForm.checkPass" type="password" autocomplete="new-password" />
      </a-form-model-item>
    </a-form-model>
  </a-modal>
</template>
<script>
export default {
  data () {
    const checkName = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        callback()
      }
    }
    const checkUserType = (rule, value, callback) => {
      if (!value && value !== 0) {
        callback(new Error('选择用户类型'))
      } else {
        callback()
      }
    }
    const validatePassword = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPassword !== '') {
          this.$refs.ruleForm.validateField('checkPassword')
        }
        callback()
      }
    }
    const validatePass2 = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入确认密码'))
      } else if (value !== this.ruleForm.password) {
        callback(new Error('两次密码输入不一致'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        password: '',
        checkPass: '',
        name: '',
        userType: undefined
      },
      rules: {
        password: [{ validator: validatePassword, trigger: 'change' }],
        checkPass: [{ validator: validatePass2, trigger: 'change' }],
        name: [{ validator: checkName, trigger: 'change' }],
        userType: [{ validator: checkUserType, trigger: 'change' }]
      },
      layout: {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 }
      },
      visible: false
    }
  },
  props: {
    title: {
      type: String,
      default: '创建用户'
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$emit('comfirmUser', this.ruleForm)
          this.visible = false
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>
