<template>
  <a-modal v-model="visible" :title="title" @ok="submitForm('ruleForm')" @cancel="resetForm('ruleForm')">
    <a-form-model ref="ruleForm" :model="ruleForm" :rules="rules" v-bind="layout">
      <a-form-model-item label="车辆号码" prop="carNumber">
        <a-input v-model.number="ruleForm.carNumber" />
      </a-form-model-item>
      <a-form-model-item label="联系电话" prop="phone">
        <a-input v-model="ruleForm.phone" autocomplete="off" />
      </a-form-model-item>
      <a-form-model-item label="检验日期" prop="testDate">
        <a-date-picker v-model="ruleForm.testDate" autocomplete="off" />
      </a-form-model-item>
      <a-form-model-item label="下次年审" prop="nextDate">
        <a-date-picker v-model="ruleForm.nextDate" autocomplete="off" />
      </a-form-model-item>
      <a-form-model-item label="状态" prop="state">
        <a-select placeholder="请选择" v-model="ruleForm.state">
          <a-select-option :value="item.id" v-for="item in stateList" :key="item.id">
            {{item.name}}
          </a-select-option>
        </a-select>
        <!-- <a-input v-model="ruleForm.status" type="password" autocomplete="off" /> -->
      </a-form-model-item>
      <a-form-model-item label="备注" prop="commonLog">
        <a-input v-model="ruleForm.commonLog" autocomplete="off" />
      </a-form-model-item>
    </a-form-model>
  </a-modal>
</template>
<script>
const messageObj = {
  carNumber: '请输入车牌号码',
  phone: '请输入联系电话',
  testDate: '请选择检验日期',
  nextDate: '请选择下次年审日期',
  state: '请选择状态',
  commonLog: '请输入备注'
}
export default {
  data () {
    const checkRule = (rule, value, callback) => {
      if (!value && value !== 0) {
        callback(new Error(messageObj[rule.field]))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        carNumber: '',
        phone: '',
        testDate: undefined,
        nextDate: undefined,
        state: undefined,
        commonLog: ''
      },
      rules: {
        carNumber: [{ validator: checkRule, trigger: 'change' }],
        phone: [{ validator: checkRule, trigger: 'change' }],
        testDate: [{ validator: checkRule, trigger: 'change' }],
        nextDate: [{ validator: checkRule, trigger: 'change' }],
        state: [{ validator: checkRule, trigger: 'change' }]
        // commonLog: [{ validator: checkRule, trigger: 'change' }]
      },
      layout: {
        labelCol: { span: 4 },
        wrapperCol: { span: 20 }
      },
      visible: false,
      stateList: [
        {
          id: 1,
          name: '今年已来'
        },
        {
          id: 2,
          name: '没有来'
        }
      ]
    }
  },
  props: {
    title: {
      type: String,
      default: '创建用户'
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log('可以提交', this.ruleForm)
          this.visible = false
          this.$emit('comfirm', this.ruleForm)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>
