<template>
  <div class="v-layout">
    <a-layout id="components-layout-demo-custom-trigger">
      <a-layout-sider :trigger="null">
        <!-- <div class="logo" /> -->
        <div class="logo">
          汽车检测中心
        </div>
        <a-menu theme="dark" mode="inline" :default-selected-keys="['1']">
          <a-menu-item :key="item.id" v-for="item in menu" @click="handleMenuClick(item)">
            <a-icon :type="item.icon" />
            <span>{{item.name}}</span>
          </a-menu-item>
        </a-menu>
      </a-layout-sider>
      <a-layout>
        <a-layout-header style="background: #fff; padding: 0">
          <Vheader></Vheader>
        </a-layout-header>
        <a-layout-content
          :style="{ margin: '16px', padding: '24px', background: '#fff', minHeight: '280px', overflow: 'auto' }"
        >
          <slot></slot>
        </a-layout-content>
      </a-layout>
    </a-layout>
  </div>
</template>
<script>
import Vheader from '@/components/VHeader'
import { checkType } from '@/apis//user-management.js'
import { mapActions } from 'vuex'
export default {
  name: 'v-layout',
  components: { Vheader },
  data () {
    return {
      menu: [
        {
          name: '数据搜索',
          url: '/data-center',
          id: '1',
          icon: 'database'
        },
        {
          name: '用户管理',
          url: '/user-management',
          id: '2',
          icon: 'user'
        }
      ]
    }
  },
  mounted () {
    this.checkType().then(() => {
      this.handleMenuClick(this.menu[0])
    })
  },
  methods: {
    ...mapActions(['setUserInfo']),
    checkType () {
      return checkType().then(res => {
        if (!res.success) {
          window.location = './login.html'
        } else {
          this.setUserInfo(res.value)
          // 0普通用户,1管理员
          if (res.value.type === 0) {
            this.menu = [this.menu[0]]
          }
        }
      })
    },
    handleMenuClick (item) {
      this.$router.push({ path: item.url })
    }
  }
}
</script>
<style lang="less" scoped>
.v-layout {
  width: 100%;
  height: 100%;
}
#components-layout-demo-custom-trigger .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  transition: color 0.3s;
}

#components-layout-demo-custom-trigger .trigger:hover {
  color: #1890ff;
}

#components-layout-demo-custom-trigger .logo {
  height: 60px;
  line-height: 32px;
  text-align: center;
  color: #fff;
  font-size: 20px;
  background: #082c4e;
  padding: 16px;
}
</style>
